    @media screen and (max-width:360px) {
    
      .hero-section {
        background-size:cover;
        background-position:center;
        height:auto;
        width:100%;
        overflow:hidden;
        object-fit: cover;
      }

      .hero-section .container .row .col-sm-6:nth-child(2){
        margin-bottom: 4rem;
      }

      .svg {
        width:200px;
        margin:0 auto;
        display:block;
        padding:0;
      }

     

      #about , #service, #portfolio , #contact {
        text-align:center;
      }

      .underline {
        margin:0 auto;
        display:block;
      }
      

    }

    .down-arrow {
      animation:topToBottom .5s linear infinite alternate;
    }

    .text-cobalt {
      color:#651FFF;
    }

    .icon {
      opacity:.7s
    }

    .icon:hover {
      opacity: 1;
      cursor: pointer;
    }

    .icon1 {
      font-size:24px;
      color:indigo;
     
    }
     .icon1:hover {
           cursor: pointer;
    }


    @keyframes topToBottom {
      from {
        transform:translateY(-10px);
      }
      to {
        transform:translateY(0px);
      }
    }

